@import url(../fontawesome-free-5.5.0-web/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

/*
* White space reset */
* {
  padding: 0;
  margin: 0; }

/* Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

p, ul, ol {
  -webkit-padding-after: 1.5em;
  padding-block-end: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

@-webkit-keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

form {
  -webkit-animation: shake 0.5s 1 ease-in-out forwards;
  animation: shake 0.5s 1 ease-in-out forwards; }

fieldset,
fieldset legend {
  border: 2px solid #CCC;
  border-radius: 0.3em;
  -webkit-box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.1); }

fieldset {
  background: #FFF;
  padding: 3.5vh 4.5vw 3vh 4.5vw; }

fieldset legend {
  display: inline-block;
  background: #FFF;
  padding: 0.6em 2.2em;
  font-weight: bold; }

label {
  font-weight: bold;
  display: block; }

input[type="text"],
input[type="password"] {
  width: 20rem;
  display: block;
  border: 2px solid #CCC;
  padding: 0.4em 1em;
  border-radius: 0.4em;
  background: #EEE;
  outline: none;
  -webkit-transition: background 0.3s ease, border-color 0.3s ease;
  transition: background 0.3s ease, border-color 0.3s ease; }

input[type="text"]:focus,
input[type="password"]:focus {
  background: #FFF;
  border-color: #333; }

input[type="submit"] {
  display: block;
  border: 2px solid #CCC;
  background: #333;
  color: #FFF;
  padding: 0.6em 2.2em;
  border-radius: 0.4em;
  font-weight: bold;
  cursor: pointer; }

input[type="submit"]:hover {
  background: #060;
  -webkit-transition: background 0.3s ease, border-color 0.3s ease;
  transition: background 0.3s ease, border-color 0.3s ease; }

.button {
  display: inline-block;
  padding: 1em 1.5em;
  border-radius: 0.4em;
  font-weight: bold;
  margin: 0 2rem 2rem 0;
  background: #EEE;
