.button.deep-purple {
  background: #673AB7;
  color: #ffffff; }

.button.indigo {
  background: #3F51B5;
  color: #ffffff; }

.button.brown {
  background: #795548;
  color: #ffffff; }

.button.yellow {
  background: #FFEB3B;
  color: #000000; }

body {
  color: var(--color-text-main, red); }

.site {
  padding: var(--spacing-site, 2em); }
  .site-content {
    max-width: 60rem;
    margin: 0 auto;
    margin-top: 10rem; }

.body.login {
  height: 100vh;
  overflow: hidden;
  padding-top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
  .body.login .site {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    background: #EEE;
    border: 4rem solid #FFF;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
