.navigation-main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* display: flex; */
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
  display: none; }

.navigation-main ul {
  padding: 0;
  margin: 0; }

.navigation-main li {
  display: block;
  list-style: none outside;
  padding: 1em 0;
  -webkit-animation: navitemin 1s 1 ease forwards;
  animation: navitemin 1s 1 ease forwards;
  opacity: 0;
  -webkit-transform: scale(0.1) translate(0, 3rem) rotate(720deg);
  transform: scale(0.1) translate(0, 3rem) rotate(720deg); }

.navigation-main li:nth-child(1) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

.navigation-main li:nth-child(2) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.navigation-main li:nth-child(3) {
  -webkit-animation-delay: 0.45s;
  animation-delay: 0.45s; }

.navigation-main li:nth-child(4) {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s; }

.navigation-main li:nth-child(5) {
  -webkit-animation-delay: 0.75s;
  animation-delay: 0.75s; }

.navigation-main li:nth-child(6) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s; }

.navigation-main a {
  display: block;
  padding: 1.5em 2.5em;
  font-size: 1.5em;
  background: #FFF;
  text-decoration: none;
  color: #444;
  -webkit-box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem; }

.navigation-main a:before {
  margin-right: 1em; }

#nav-main-toggle {
  display: none; }

#nav-main-toggle:checked + .site-header .navigation-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1000; }

#nav-main-toggle:checked + .site-header .nav-toggle:before {
  content: '\f00d'; }

#nav-main-toggle:checked + .site-header .nav-toggle {
  z-index: 1010;
  -webkit-animation: jump 0.3s 1 ease-in-out forwards;
  animation: jump 0.3s 1 ease-in-out forwards; }

.nav-toggle {
  position: fixed;
  top: var(--spacing-site, 2rem);
  left: var(--spacing-site, 2rem);
  z-index: 1000;
  font-size: 2rem;
  padding: 0 0.5rem;
  -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
  animation: navtogglein 0.5s 1 ease-in-out forwards; }
  .nav-toggle span {
    display: none; }
  .nav-toggle:before {
    margin-right: 1rem;
    content: '\f0c9'; }

#nav-toggle-right {
  left: inherit;
  right: var(--spacing-site, 2rem); }

